export default {
	name: "Commodity",
	props: {
		colCount: {
			type: Number | Number,
			default: 2,
			validator: (v) => Number.isInteger(Number(v))
		},
		wrap: {
			type: Boolean,
			default: true
		},
		colW: {
			type: String | Number,
			default: ''
		},
		dataList: {
			type: Array,
			default: () => []
		}
	},
	computed: {
		itemW() {
			if (this.colW) {
				return {
					width: `${this.colW}rpx`
				}
			} else {
				return {
					width: `${100 / Number(this.colCount)}%`
				}
			}
		},
		shouldWrap() {
			return this.wrap ? {
				flexWrap: 'wrap'
			} : ''
		}
	},
	methods: {

	}
}
